/**
 * Default base for input components.
 */
export default {
  props: {
    context: {
      type: Object,
      required: true
    }
  },
  computed: {
    type: function type() {
      return this.context.type;
    },
    attributes: function attributes() {
      return this.context.attributes || {};
    },
    hasValue: function hasValue() {
      return this.context.hasValue;
    }
  }
};